<template><section><html><head></head><body><h1>VcViewer</h1>
<p><code v-pre="">vc-viewer</code> is a container of Cesium core class and instance. It's the root component of other components of vue-cesium.
The container is an empty DOM node which used to mount other components or DOM nodes. In <code v-pre="">2.0.1 +</code> version, you can use <code v-pre="">ref</code> to get the<code v-pre="">createPromise</code> object of the component to perform related operations.</p>
<h2>Example</h2>
<h3>Initialize the Viewer instance with the vc-viewer component</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer" ref="viewerContainer">
      <vc-viewer ref="vcViewer" :animation="animation" :base-layer-picker="baseLayerPicker" :timeline="timeline" :fullscreen-button="fullscreenButton" :fullscreen-element="fullscreenElement" :info-box="infoBox" @ready="ready">
        <vc-navigation></vc-navigation>
        <vc-layer-imagery ref="layerText">
          <vc-provider-imagery-tianditu map-style="eia_c" :token="tk"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu map-style="img_c" :token="tk"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>animation</span>
        <md-switch v-model="animation"></md-switch>
        <span>timeline</span>
        <md-switch v-model="timeline"></md-switch>
        <span>baseLayerPicker</span>
        <md-switch v-model="baseLayerPicker"></md-switch>
        <span>fullscreenButton</span>
        <md-switch v-model="fullscreenButton"></md-switch>
        <span>infoBox</span>
        <md-switch v-model="infoBox"></md-switch>
      </div>
    </div>
  </template>
  
</doc-preview>
