<tbody>
<tr>
<td>selectedEntityChanged</td>
<td>Entity</td>
<td>Gets the event that is raised when the selected entity changes.</td>
<td>Viewer</td>
</tr>
<tr>
<td>trackedEntityChanged</td>
<td>Entity</td>
<td>Gets the event that is raised when the tracked entity changes.</td>
<td>Viewer</td>
</tr>
<tr>
<td>layerAdded</td>
<td>imageryLayer, index</td>
<td>An event that is raised when a layer is added to the collection. Event handlers are passed the layer that was added and the index at which it was added.</td>
<td>Viewer.imageryLayers</td>
</tr>
<tr>
<td>layerMoved</td>
<td>imageryLayer, newIndex, oldIndex</td>
<td>An event that is raised when a layer changes position in the collection. Event handlers are passed the layer that was moved, its new index after the move, and its old index prior to the move.</td>
<td>Viewer.imageryLayers</td>
</tr>
<tr>
<td>layerRemoved</td>
<td>imageryLayer, index</td>
<td>An event that is raised when a layer is removed from the collection. Event handlers are passed the layer that was removed and the index from which it was removed.</td>
<td>Viewer.imageryLayers</td>
</tr>
<tr>
<td>layerShownOrHidden</td>
<td>imageryLayer, index, flag</td>
<td>An event that is raised when a layer is shown or hidden by setting the ImageryLayer#show property. Event handlers are passed a reference to this layer, the index of the layer in the collection, and a flag that is true if the layer is now shown or false if it is now hidden.</td>
<td>iewer.imageryLayers</td>
</tr>
<tr>
<td>dataSourceAdded</td>
<td>dataSource</td>
<td>An event that is raised when a data source is added to the collection. Event handlers are passed the data source that was added.</td>
<td>Viewer.dataSources</td>
</tr>
<tr>
<td>dataSourceMoved</td>
<td>dataSource</td>
<td>An event that is raised when a data source changes position in the collection. Event handlers are passed the data source that was moved, its new index after the move, and its old index prior to the move.</td>
<td>Viewer.dataSources</td>
</tr>
<tr>
<td>dataSourceRemoved</td>
<td>dataSource</td>
<td>An event that is raised when a data source is removed from the collection. Event handlers are passed the data source that was removed.</td>
<td>Viewer.entities</td>
</tr>
<tr>
<td>collectionChanged</td>
<td>collection, added, removed, changed</td>
<td>Gets the event that is fired when entities are added or removed from the collection. The generated event is a EntityCollection.collectionChangedEventCallback.</td>
<td>Viewer.entities</td>
</tr>
<tr>
<td>morphComplete</td>
<td>object</td>
<td>The event fired at the completion of a scene transition.</td>
<td>Viewer.scene</td>
</tr>
<tr>
<td>morphStart</td>
<td>object</td>
<td>The event fired at the beginning of a scene transition.</td>
<td>Viewer.scene</td>
</tr>
<tr>
<td>postRender</td>
<td>scene</td>
<td>Gets the event that will be raised immediately after the scene is rendered. Subscribers to the event receive the Scene instance as the first parameter and the current time as the second parameter.</td>
<td>Viewer.scene</td>
</tr>
<tr>
<td>preRender</td>
<td>scene</td>
<td>Gets the event that will be raised after the scene is updated and immediately before the scene is rendered. Subscribers to the event receive the Scene instance as the first parameter and the current time as the second parameter.</td>
<td>Viewer.scene</td>
</tr>
<tr>
<td>postUpdate</td>
<td>scene</td>
<td>Gets the event that will be raised immediately after the scene is updated and before the scene is rendered. Subscribers to the event receive the Scene instance as the first parameter and the current time as the second parameter.</td>
<td>Viewer.scene</td>
</tr>
<tr>
<td>preUpdate</td>
<td>scene</td>
<td>Gets the event that will be raised before the scene is updated or rendered. Subscribers to the event receive the Scene instance as the first parameter and the current time as the second parameter.</td>
<td>Viewer.scene</td>
</tr>
